import { useState } from "react";
import Axios from "axios";

import styled from "styled-components";
import "./App.css";

import {
  Header,
  AppNameComponent,
  SearchComponent,
  SearchInput,
} from "./components/Header";

import RecipieComponent from "./components/RecipieComponent";

const API_ID = "34177dca";
const API_KEY = "2a54ffeec20b55d1c45759a1ea73d233";

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const RecipeListContainer = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 30px;
  gap: 20px;
  justify-content: space-evenly;
`;

function App() {
  const [timeOutId, updateTimeoutId] = useState();
  const [recipeList, updateRecipeList] = useState([]);

  const fetchRecipe = async (searchString) => {
    const response = await Axios.get(
      `https://api.edamam.com/search?q=${searchString}&app_id=${API_ID}&app_key=${API_KEY}`
    );
    updateRecipeList(response.data.hits);
  };

  const onTextChange = (event) => {
    clearTimeout(timeOutId);
    const timeout = setTimeout(() => fetchRecipe(event.target.value), 500);
    updateTimeoutId(timeout);
  };

  return (
    <Container>
      <Header>
        <AppNameComponent>Recipe Finder</AppNameComponent>
        <SearchComponent>
          <SearchInput placeholder="Search Recipe" onChange={onTextChange} />
        </SearchComponent>
      </Header>
      <RecipeListContainer>
        {recipeList.length &&
          recipeList.map((recipeObj) => (
            <RecipieComponent recipeObj={recipeObj.recipe} />
          ))}
      </RecipeListContainer>
    </Container>
  );
}

export default App;
