import { useState } from "react";
import styled from "styled-components";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Dialog from "@material-ui/core/Dialog";

export const RecipeContainer = styled.div`
  display: flex;
  flex-direction: column;
  padding: 30px;
  gap: 20px;
  box-shadow: 0 3px 10px 0 #aaa;
  width: 300px;
`;

export const RecipeName = styled.span`
  font-size: 18px;
  font-weight: bold;
  color: black;
  margin: 0 0;
`;

export const Ingredients = styled.span`
  font-size: 18px;
  border: solid 1px green;
  color: black;
  margin: 0 0;
  cursor: pointer;
  padding: 10px 15px;
  border-radius: 12px;
  color: green;
  text-align: center;
  margin-bottom: -6px;
`;

export const SeeMoreText = styled(Ingredients)`
  color: #eb3300;
  border: solid 1px #eb3300;
`;

export const CoverImage = styled.img`
  height: 200px;
  object-fit: cover;
`;

const RecipieComponent = (props) => {
  const [show, setShow] = useState(false);
  const { recipeObj } = props;
  return (
    <>
      <Dialog open={show}>
        <DialogTitle id="alert-dialog-slide-title">Ingredients</DialogTitle>
        <DialogContent>
          <table>
            <thead>
              <th>Ingredients</th>
              <th>Weight</th>
            </thead>
            <tbody>
              {recipeObj.ingredients.map((ingredientsObj) => (
                <tr>
                  <td>{ingredientsObj.text}</td>
                  <td>{ingredientsObj.weights}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </DialogContent>
        <DialogActions>
          <Ingredients onClick={() => window.open(recipeObj.url)}>
            See More
          </Ingredients>
          <SeeMoreText onClick={() => setShow("")}>Close</SeeMoreText>
        </DialogActions>
      </Dialog>
      <RecipeContainer>
        <CoverImage src={recipeObj.image} />
        <RecipeName>{recipeObj.label}</RecipeName>
        <Ingredients onClick={() => setShow(true)}>Ingredients</Ingredients>
        <SeeMoreText onClick={() => window.open(recipeObj.url)}>
          See Complete Recipe
        </SeeMoreText>
      </RecipeContainer>
    </>
  );
};
export default RecipieComponent;
